/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}
/* 2. Remove default margin */
* {
  margin: 0;
}
body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}
/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}
/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}
/*
  9. Create a root stacking context (Only needed with React, etc.
*/
#root,
#__next {
  isolation: isolate;
}

/* ACTUAL CSS */

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  font-family: Roboto, sans-serif;
  font-size: 1rem;
  color: rgb(212, 212, 212);
  background: #1b1530;
  min-height: 100vh;
  padding: 20px;
  overflow: auto;
}

header,
footer {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 0.8em;
  height: 10%;
  max-height: 40px;
}

header > h1 {
  user-select: none;
  cursor: pointer;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.user-interaction-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  max-width: 450px;
  margin-top: 40px;
}

.welcome-container {
  font-size: 1.2em;
  margin-bottom: 80px;
}

form {
  text-align: center;
  width: 100%;
  margin-bottom: 40px;
}

.location-label {
  display: block;
}

.input-container {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  gap: 20px;

  width: 100%;
  margin-top: 20px;
  vertical-align: bottom;
}

.searchbar-container {
  display: flex;
  align-items: flex-end;
  flex-wrap: nowrap;
}

.location-label {
  font-size: 1.1em;
  margin-bottom: 5px;
}

.location-input {
  background: rgba(212, 212, 212, 0.1);
  color: inherit;
  width: 100%;
  height: 2rem;
  padding: 5px 10px;
  border: 1px solid rgb(212, 212, 212);
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  box-shadow: inset 2px 1px 5px black;
}

.location-input::placeholder {
  color: rgb(212, 212, 212);
}

button {
  height: 2rem;
  border: 1px solid rgb(212, 212, 212);
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding-right: 10px;
  cursor: pointer;
}

.switch-container {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.unit-switch {
  position: relative;
  font-size: inherit;
  color: inherit;
  background: transparent;
  width: 4em;
  height: 2em;
  box-sizing: content-box;
  border: 1px solid;
  border-radius: 1em;
  vertical-align: bottom;
  margin: auto;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
}

.unit-switch::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;

  background: currentColor;
  width: 1.7em;
  height: 1.7em;
  margin: 0 0.15em;
  border: 1px solid;
  border-radius: 50%;
  box-sizing: border-box;
  transform: translate(0, -50%);
  transition-duration: 0.3s;
}

.unit-switch:checked::before {
  transform: translate(2rem, -50%);
  transition-duration: 0.3s;
}

.unit-container {
  display: inline-flex;
  justify-content: space-evenly;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;

  width: 100%;
  user-select: none;
  z-index: -1;
}

.weather-display {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin-bottom: 40px;
}

.error-message {
  color: red;
}

.loading-circle {
  border: 12px solid rgb(212, 212, 212);
  border-top: 12px solid rgb(27, 21, 48);
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin: 40px;
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.today-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.today-card,
.today-details-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  max-width: 450px;
  height: 450px;
  width: 100%;
  border: 2px solid #111;
  padding: 20px;
}

.today-card {
  background-repeat: no-repeat;
  background-position: 50% 40%;
  background-size: 75%;
  border-top-left-radius: 2em;
  border-top-right-radius: 2em;
}

.upper-card-content,
.lower-card-content {
  display: flex;
  flex-direction: column;
}

.upper-card-content > h2 {
  font-size: 1.3em;
  margin-bottom: 10px;
}

.lower-card-content p:first-child {
  font-size: 3em;
  line-height: 1.1em;
}

.lower-card-content p:last-child {
  margin-top: 15px;
}

.today-details-card {
  background: rgba(17, 17, 17, 0.6);
  border-bottom-right-radius: 2em;
  border-bottom-left-radius: 2em;
}

.today-details-card > div {
  display: flex;
  justify-content: space-between;

  padding: 20px;
  border-bottom: 2px solid rgba(212, 212, 212, 0.2);
}

.today-details-card > div:last-child {
  border: none;
}

.today-details-card > div > div {
  display: flex;
  align-items: center;
  gap: 20px;

  font-size: 0.7em;
}

.today-details-card > div > div i {
  font-size: 1.5em;
}

.forecast-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;

  background: rgba(17, 17, 17, 0.6);
  width: 100%;
  max-width: 900px;
  border: 2px solid #111;
  border-radius: 2em;
  padding: 20px;
  margin-top: 10px;
}

.day-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 40px;

  background: #1b1530;
  background-repeat: no-repeat;
  background-position: 95% 50%;
  background-size: 50%;
  border: 2px solid #111;
  border-radius: 2em;
  padding: 20px;
  box-shadow: 0 0 5px rgba(212, 212, 212, 0.25);
}

.given-day-temperatures {
  font-size: 1.1em;
  line-height: 1em;
  font-weight: bold;
}

@media only screen and (min-width: 900px) {
  .today-container {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
  }

  .today-card {
    border-radius: 0;
    border-top-left-radius: 2em;
    border-bottom-left-radius: 2em;
  }

  .today-details-card {
    border-radius: 0;
    border-top-right-radius: 2em;
    border-bottom-right-radius: 2em;
  }

  .today-details-card > div > div {
    font-size: 0.9em;
  }

  .forecast-container {
    flex-direction: row;
  }
}
