: center;\n  width: 100%;\n  margin-bottom: 40px;\n}\n\n.location-label {\n  display: block;\n}\n\n.input-container {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: flex-end;\n  gap: 20px;\n\n  width: 100%;\n  margin-top: 20px;\n  vertical-align: bottom;\n}\n\n.searchbar-container {\n  display: flex;\n  align-items: flex-end;\n  flex-wrap: nowrap;\n}\n\n.location-label {\n  font-size: 1.1em;\n  margin-bottom: 5px;\n}\n\n.location-input {\n  background: rgba(212, 212, 212, 0.1);\n  color: inherit;\n  width: 100%;\n  height: 2rem;\n  padding: 5px 10px;\n  border: 1px solid rgb(212, 212, 212);\n  border-top-left-radius: 1rem;\n  border-bottom-left-radius: 1rem;\n  box-shadow: inset 2px 1px 5px black;\n}\n\n.location-input::placeholder {\n  color: rgb(212, 212, 212);\n}\n\nbutton {\n  height: 2rem;\n  border: 1px solid rgb(212, 212, 212);\n  border-top-right-radius: 1rem;\n  border-bottom-right-radius: 1rem;\n  padding-right: 10px;\n  cursor: pointer;\n}\n\n.switch-container {\n  display: inline-flex;\n  justify-content: flex-start;\n  align-items: center;\n  position: relative;\n}\n\n.unit-switch {\n  position: relative;\n  font-size: inherit;\n  color: inherit;\n  background: transparent;\n  width: 4em;\n  height: 2em;\n  box-sizing: content-box;\n  border: 1px solid;\n  border-radius: 1em;\n  vertical-align: bottom;\n  margin: auto;\n  appearance: none;\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  cursor: pointer;\n}\n\n.unit-switch::before {\n  content: '';\n  position: absolute;\n  top: 50%;\n  left: 0;\n\n  background: currentColor;\n  width: 1.7em;\n  height: 1.7em;\n  margin: 0 0.15em;\n  border: 1px solid;\n  border-radius: 50%;\n  box-sizing: border-box;\n  transform: translate(0, -50%);\n  transition-duration: 0.3s;\n}\n\n.unit-switch:checked::before {\n  transform: translate(2rem, -50%);\n  transition-duration: 0.3s;\n}\n\n.unit-container {\n  display: inline-flex;\n  justify-content: space-evenly;\n  position: absolute;\n  top: 50%;\n  transform: translateY(-50%);\n  left: 0;\n\n  width: 100%;\n  user-select: none;\n  z-index: -1;\n}\n\n.weather-display {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  flex-wrap: wrap;\n  width: 100%;\n  height: 100%;\n  margin-bottom: 40px;\n}\n\n.error-message {\n  color: red;\n}\n\n.loading-circle {\n  border: 12px solid rgb(212, 212, 212);\n  border-top: 12px solid rgb(27, 21, 48);\n  border-radius: 50%;\n  width: 80px;\n  height: 80px;\n  margin: 40px;\n  animation: rotate 2s linear infinite;\n}\n\n@keyframes rotate {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.today-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  flex-wrap: wrap;\n  width: 100%;\n  height: 100%;\n}\n\n.today-card,\n.today-details-card {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n\n  max-width: 450px;\n  height: 450px;\n  width: 100%;\n  border: 2px solid #111;\n  padding: 20px;\n}\n\n.today-card {\n  background-repeat: no-repeat;\n  background-position: 50% 40%;\n  background-size: 75%;\n  border-top-left-radius: 2em;\n  border-top-right-radius: 2em;\n}\n\n.upper-card-content,\n.lower-card-content {\n  display: flex;\n  flex-direction: column;\n}\n\n.upper-card-content > h2 {\n  font-size: 1.3em;\n  margin-bottom: 10px;\n}\n\n.lower-card-content p:first-child {\n  font-size: 3em;\n  line-height: 1.1em;\n}\n\n.lower-card-content p:last-child {\n  margin-top: 15px;\n}\n\n.today-details-card {\n  background: rgba(17, 17, 17, 0.6);\n  border-bottom-right-radius: 2em;\n  border-bottom-left-radius: 2em;\n}\n\n.today-details-card > div {\n  display: flex;\n  justify-content: space-between;\n\n  padding: 20px;\n  border-bottom: 2px solid rgba(212, 212, 212, 0.2);\n}\n\n.today-details-card > div:last-child {\n  border: none;\n}\n\n.today-details-card > div > div {\n  display: flex;\n  align-items: center;\n  gap: 20px;\n\n  font-size: 0.7em;\n}\n\n.today-details-card > div > div i {\n  font-size: 1.5em;\n}\n\n.forecast-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  gap: 20px;\n\n  background: rgba(17, 17, 17, 0.6);\n  width: 100%;\n  max-width: 900px;\n  border: 2px solid #111;\n  border-radius: 2em;\n  padding: 20px;\n  margin-top: 10px;\n}\n\n.day-container {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  gap: 40px;\n\n  background: #1b1530;\n  background-repeat: no-repeat;\n  background-position: 95% 50%;\n  background-size: 50%;\n  border: 2px solid #111;\n  border-radius: 2em;\n  padding: 20px;\n  box-shadow: 0 0 5px rgba(212, 212, 212, 0.25);\n}\n\n.given-day-temperatures {\n  font-size: 1.1em;\n  line-height: 1em;\n  font-weight: bold;\n}\n\n@media only screen and (min-width: 900px) {\n  .today-container {\n    flex-direction: row;\n    justify-content: center;\n    align-items: flex-start;\n  }\n\n  .today-card {\n    border-radius: 0;\n    border-top-left-radius: 2em;\n    border-bottom-left-radius: 2em;\n  }\n\n  .today-details-card {\n    border-radius: 0;\n    border-top-right-radius: 2em;\n    border-bottom-right-radius: 2em;\n  }\n\n  .today-details-card > div > div {\n    font-size: 0.9em;\n  }\n\n  .forecast-container {\n    flex-direction: row;\n  }\n}\n"],"sourceRoot":""}]);
